.spinner-container {
 display: flex;
 justify-content: center;
 align-items: center;
 height: 100px;
 position: relative;
 top: 200px;
}

.ball {
    width: 80px;
    height: 80px;
    margin: 0 5px;
    border-radius: 50%;
    background-color: green;
    animation: bounce 0.4s infinite alternate;
}

.ball:nth-child(2) {
    animation-delay: 0.2s;
}

.ball:nth-child(3) {
    animation-delay: 0.4s;
}

@keyframes bounce {
    0% {
        transform: translateY(0)
    }
    100% {
        transform: translateY(-40px);
    }
}